import React from 'react';
import { InjectedFormProps, reduxForm } from 'redux-form';

import { Flex } from '@rebass/grid';

import { Hr, ISpinner, OkCancelButtons, withSpinner } from 'components';
import { customerStatusConst, formNamesConst, identificationTypesConst } from 'consts';
import { CustomerInfo } from './../../components';

import {
  THandleAddCustomer,
  THandleGetDictionaryCountries,
  THandleUpdateCustomer,
} from 'store';

import { ISelectValue } from 'types';

interface IEditCustomerForm extends ISpinner {
  addCustomer: THandleAddCustomer;
  countryCodes: Array<ISelectValue>;
  countryCodeValue: ISelectValue;
  currentCustomerStatus: string | number;
  customerStatusOptions: Array<ISelectValue>;
  identificationTypeValue: ISelectValue;
  institutionsOptions: Array<ISelectValue>;
  isCountryCodesLoading: boolean;
  isEditMode?: boolean;
  isLoadingCustomerStatuses: boolean;
  isReadOnly?: boolean;
  loadCountryCodes: THandleGetDictionaryCountries;
  onCancel: () => void;
  updateCustomer: THandleUpdateCustomer;
}

type TEditCustomerForm = IEditCustomerForm & InjectedFormProps<{}, IEditCustomerForm>;

const EditCustomerForm: React.FC<TEditCustomerForm> = ({
  addCustomer,
  countryCodes,
  countryCodeValue,
  currentCustomerStatus,
  customerStatusOptions,
  dirty,
  handleSubmit,
  identificationTypeValue,
  institutionsOptions,
  isCountryCodesLoading,
  isEditMode,
  isLoadingCustomerStatuses,
  isReadOnly,
  loadCountryCodes,
  onCancel,
  pristine,
  updateCustomer,
}) => {
  React.useEffect(
    () => {
      loadCountryCodes();
    },
    [loadCountryCodes]
  );

  const submitAction = React.useMemo(
    () => isEditMode ? updateCustomer : addCustomer,
    [isEditMode, updateCustomer, addCustomer]
  );

  // eslint-disable-next-line
  const handleSubmitForm = React.useCallback(
    handleSubmit(submitAction),
    [handleSubmit, submitAction]
  );

  const isIdentification = React.useMemo(
    () => identificationTypeValue
      && identificationTypeValue.value !== identificationTypesConst.NO_IDENTIFICATION,
    [identificationTypeValue]
  );

  const isUSACountryCode = React.useMemo(
    () => countryCodeValue && countryCodeValue.value === 'USA',
    [countryCodeValue]
  );

  const customerStatusOptionsPrepared = React.useMemo(
    () => {
      interface IStatusesConfig {
        from: customerStatusConst,
        to: Array<customerStatusConst>
      }

      const statusesConfig: Array<IStatusesConfig> = [
        { from: customerStatusConst.PENDING, to: [customerStatusConst.PENDING, customerStatusConst.DELETED], },
        { from: customerStatusConst.ACTIVE, to: [customerStatusConst.ACTIVE, customerStatusConst.SUSPENDED], },
        { from: customerStatusConst.INACTIVE, to: [customerStatusConst.INACTIVE, customerStatusConst.SUSPENDED, customerStatusConst.CLOSED], },
        { from: customerStatusConst.SUSPENDED, to: [customerStatusConst.SUSPENDED, customerStatusConst.CLOSED], },
        { from: customerStatusConst.CLOSED, to: [customerStatusConst.CLOSED, customerStatusConst.DELETED], },
      ];

      const options: Array<ISelectValue> = customerStatusOptions;

      const currentState = statusesConfig.find(el => el.from === currentCustomerStatus);
      const statusesTo = currentState && currentState.to;

      if (statusesTo) {
        options.map(option => statusesTo.find(status => status === option.value) ? option.isDisabled = false : option.isDisabled = true);
      }

      return options;
    },
    [currentCustomerStatus, customerStatusOptions]
  );

  return (
    <form onSubmit={isReadOnly ? null : handleSubmitForm}>
      <CustomerInfo
        countryCodes={countryCodes}
        customerStatusOptions={customerStatusOptionsPrepared}
        institutionsOptions={institutionsOptions}
        isCountryCodesLoading={isCountryCodesLoading}
        isCounty={isUSACountryCode}
        isEditMode={isEditMode}
        isIdentification={isIdentification}
        isLoadingCustomerStatuses={isLoadingCustomerStatuses}
        isReadOnly={isReadOnly}
      />
      <Hr />
      <Flex justifyContent="flex-end">
        <OkCancelButtons
          okText="Save"
          cancelText="Close"
          onCancel={onCancel}
          withCancelConfirmation={dirty}
          disabledOk={pristine}
          hideOk={isReadOnly}
        />
      </Flex>
    </form >
  );
};

export default reduxForm<{}, IEditCustomerForm>({
  form: formNamesConst.CUSTOMER,
  destroyOnUnmount: true,
  enableReinitialize: true,
})(withSpinner()(EditCustomerForm));
